import './App.css';

import NavBar from './components/navbar';
import { Route, Link } from 'react-router-dom';
import Home from './components/Home';


function App() {
  return (
    <>
      <NavBar />
      <Route exact path="/">
        <Link to='/home'> INGRESAR </Link>
      </Route>
      <Route path="/home" component={Home} />

    </>
  );
}

export default App;
